#requests-count{
    position: absolute;
    left: 10px;
    font-size: 12px;
    color: #29303b;
    background: #f0f0f0;
    padding: 5px 10px;
    font-weight: 500;
    border-radius: 35px;
}

#top-nav-left{
    position: absolute;
    left: 10px;
}

.top-nav{
    padding: 20px 0;
    height: 20px;
    width: 100%;
    position: fixed;
    text-transform: capitalize;
    top: 0;
    z-index: 3;
    text-align: center;
    background: #fafafa;
    font-weight: 700;
    color: #29303b;
    font-size: 15px;
    box-shadow: 0 0 10px rgba(150, 150, 150, 0.0);
}

.top-nav img{
    width: 20px;
    height: 20px;
}

#top-nav-right {
    position: absolute;
    right: 10px;
}

@media screen and (min-width: 738px) {
    #requests-count{
        font-size: 14px;
    }

    .top-nav{
        font-size: 16px;
    }
}
