#login {
    width: auto;
    height: 100%;
    width: 100%;
    position: fixed;
}

#login-container {
    position: relative;
    top: 50%;
    width: 100%;
    transform: translateY(-50%);
}

#logo-container {
  font-family: "Pacifico", sans-serif;
  color: #29303b;
  font-weight: 700;
  font-size: 2.5em;
  text-align: center;
}

#app-description {
  margin: 30px 15px;
  line-height: 1.8em;
  text-align: justify;
  text-align-last: center;
}

.login-button {
  width: 150px;
  padding: 10px;
  text-align: center;
  margin: 15px auto 0 auto;
  box-shadow: 0 0 10px rgba(150, 150, 150, 0.1);
  color: #29303b;
}
