#profile-contain-info{
    margin-top: 95px;
    padding: 0 10px;
    text-align: center;
}

#profile-contain-name{
    font-size: 14px;
    font-weight: 700;
    margin-top: 5px;
    color: #29303b;
}

#profile-contain-image img{
    width: 100px;
    height: 100px;
    border-radius: 100%;
    border: solid 2px #f0f0f0;
}

#profile-contain-caption{
    margin-top: 5px;
    padding: 0 5px;
    word-wrap: break-word;
    line-height: 1.7em;
}
